class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      timer: 60,
      ticking: false,
      animate: false
    }
    this.audio = new Audio(growl);
    this.audio.load();
    this.audio.addEventListener("ended", ()=>{
      console.log(this.audio);
      this.audio.load();
      this.finish();
    })

    this.soundboard = [track1,
      track2,
      track3,
      track4,
      track5,
      track6,
      track7,
      track8,
      track9,
      track10,
      track11,
      track12,
      track13,
      track14,
      track15,
      track16,
      track17,
      track18,
      track19,
      track20,
      track21,
      track22,
      track23,
      track24,
      track25,
      track26,
      track27,
      track28,
      track29,
      track30]
      .map(s => new Audio(s))
    this.soundboard.forEach(s => s.load());

    this.tickAudio = new Audio(tickaudio);
    this.tickAudio.load();
    this.tickAudio.loop = true;
  }

  start = () => {
    this.setState({
      ticking: true
    }, this.tick)
  }

  pause = () => {
    this.tickAudio.pause();
    this.tickAudio.load();
    this.setState({
      ticking: false
    })
  }

  // tick = () => {
  //   let currentTime = this.state.timer;
  //   setTimeout(()=>{
  //     if (currentTime > 1 && this.state.ticking == true) {
  //       this.setState({
  //         timer: currentTime-1,
  //       })
  //       this.tick();
  //     } else if (currentTime == 1 && this.state.ticking == true) {
  //       this.setState({
  //         timer: 0
  //       }, this.growl)
  //     }
  //   }, 1000);    
  // }

  tick = () => {
    let currentTime = this.state.timer;
    if (currentTime > 1 && this.state.ticking == true) {
      this.tickAudio.play();
    }
    let ticker = setInterval(()=>{
      if (currentTime > 1 && this.state.ticking == true) {
        currentTime--;
        this.setState({
          timer: currentTime
        })

      } else if (currentTime == 1 && this.state.ticking == true) {
        this.setState({
          timer: 0
        }, this.growl)
        this.tickAudio.pause();
        this.tickAudio.load();
        clearInterval(ticker);
      } else {
        this.tickAudio.pause();
        this.tickAudio.load();
        clearInterval(ticker);
      }

    }, 1000)
    
  }

  reset = () => {
    this.tickAudio.pause();
    this.tickAudio.load();
    this.setState({
      timer: 60,
      ticking: false
    })
  }

  growl = () => {
    console.log("done, should play audio");
    console.log(this.audio);
    this.setState({
      animate: true
    })
    this.audio.play();
    

  }

  finish = () => {
    console.log("finished");

    let index = Math.floor(Math.random() * this.soundboard.length);
    this.soundboard[index].play();

    this.setState({
      timer: 60,
      ticking: false,
      animate: false
    })
  }

  render() {
    return (
      <div className="App" style={{
        backgroundImage: `url(${background})`,
        backgroundAttachment: 'fixed',
        backgroundPosition: 'center'
      }}>
        <header className="App-header">
          {/* <img src={logo} className="App-logo" alt="logo" />
          <p>
            Edit <code>src/App.js</code> and save to reload.
          </p>
          <a
            className="App-link"
            href="https://reactjs.org"
            target="_blank"
            rel="noopener noreferrer"
          >
            Learn React
          </a> */}
          
          {/* sex-panther image */}
          {/* <img src={sexPantherImage} /> */}
          <h1>ANCHORMAN TIMER</h1>
          <img id="sex-panther-image" className={this.state.animate ? "animate" : ""} src={sexPantherImage} />
          {/* time */}
          <div>
            <h2 id="time">
              {this.state.timer}
            </h2>
          </div>
          {/* controls */}
          <div>
            <button
              onClick={this.state.ticking ? this.pause : this.start}
            >
              <img src={ this.state.ticking ? `${pauseImg}` : `${startImg}`}/>
            </button>
          </div>
          <div>
            <button onClick={this.reset}>
              <img src={`${resetImg}`}/>
            </button>
          </div>
        </header>
      </div>
    );
  }
  
}

export default App;
